@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor{
    h3{
        margin-bottom: 3rem;
    }
    padding: 3rem 0;
}
.galeria-contenedor{
    
    @include m.gridd(2,2rem);
    @include m.celular{
        @include m.gridd(3,2rem);
    }
    @include m.tablet{
        @include m.gridd(4,2rem);
    }
    @include m.desktop{
        @include m.gridd(6,2rem);
    }
}
.overlay2{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    display: grid;
    place-content: center;
    picture{
        img{
            max-width: 70rem;
        }
        p{
            font-size: 2.5rem;
            background-color: v.$blanco;
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
            text-align: center;
            margin: 4rem auto;
            font-weight: bold;
            cursor: pointer;
        }
    }
}
.fijar-body{
    overflow: hidden;
}