@use "base/variables" as v;
@use "base/mixins" as m;
.lineup{
    padding: 3rem 0;
    background-color: v.$rojo;
    h3{
        color: v.$blanco;
    }
    p{
        color: v.$blanco;
        font-size: 2rem;
        margin: 3rem 0;
    }
    .lineupGrid{
        @include m.tablet{
            @include m.gridd(2,2rem);
        }
    }
}
.lineupGrid__item{
    padding: 1rem 0;
    p{
        color: v.$negro;
        margin-top: 3rem;
    }
    li{
        font-weight: bold;
        span{
            font-weight: 400;
        }
        padding: 2rem 1rem;
    }
    margin: 2rem 0;
}
.azul{
    background-color: v.$azul;
}
.amarillo{
    background-color: v.$amarillo;
}
.horario-amarillo{
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 10%);
    }
}
.horario-azul{
    li:nth-child(odd){
        background-color: darken(v.$azul, 10%);
    }
}
.rock::after, .edm::after{
    content: "";
    width: 7rem;
    height: 7rem;
    display: block;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 2rem auto;
}
.rock::after{
    background-image: url("../../../build/img/icono_rock.png");
}
.edm::after{
    background-image: url("../../../build/img/icono_edm.png");
}