@mixin celular {
    @media(min-width:468px){
        @content;
    }
}
@mixin tablet {
    @media(min-width:768px){
        @content;
    }
}
@mixin desktop {
    @media(min-width:968px){
        @content;
    }
}
@mixin gridd($columnas, $separacion){
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    gap: $separacion;
}