@use "base/variables" as v;
@use "base/mixins" as m;
html{
    font-size: 62.5%;
    box-sizing: border-box;
}

*, *::before, *::after{
    box-sizing: inherit;
}

body{
    font-family: "Montserrat", Arial, Helvetica;
}
h1,h2,h3,h4{
    font-size: 2.8rem;
    margin: 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3.2rem;
}
h4{
    font-size: 2.8rem;
}
a{
    text-decoration: none;
}
.centrar-texto{
    text-align: center;
}
img{
    width: 100%;
}
p{
    font-size: 2.8rem;
    line-height: 1.5;
    margin: 0;
}
ul{
    margin: 0;
    padding: 0;
}
li{
    list-style: none;
    font-size: 1.6rem;
}

/*Fijar navegacion efectoScroll*/
.fijar-barra{
    @include m.tablet{
        position: fixed;
        width: 100%;
        top:0;
    }
}
.mover-body{
    @include m.tablet{
        margin-top: 8.6rem;
    }
}