@use "base/variables" as v;
@use "base/mixins" as m;

.content{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
.header{
    background-color: v.$azul;
    .cabecera{
        padding: 2rem 0;
        color: v.$blanco;
    }
    
}
.cabecera{
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }
}
.header__nav{
    .enlace{
        display: block;
        font-size: 2.5rem;
        color: v.$blanco;
        margin: 2rem;
        text-align: center;
        @include m.tablet{
            margin: auto;
        }
        &:hover{
            color: v.$amarillo;
        }
    }
    @include m.tablet{
        display: flex;
        gap: 2rem;
    }
}