@use "base/variables" as v;
@use "base/mixins" as m;
.item{
    p{
        font-size: 2rem;
    }
    .fecha{
        color: v.$rojo;
        font-weight: bold;
    }
    &:last-child{
        @include m.tablet{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
    padding: 2rem 0;
}
.contenedorInfo{
    padding: 3rem 0;
    @include m.tablet{
        @include m.gridd(2,4rem);
    }
}