@use "base/variables" as v;
@use "base/mixins" as m;
.video{
    max-height: 70rem;
    min-height: 50rem;
    position: relative;
    overflow: hidden;
    video{
        width: auto;
        height: 100%;
        @include m.desktop{
            width: 100%;
            display: block;
        }
    }
}
.overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgb(180,58,167);
    background: linear-gradient(140deg, rgba(180,58,167,0.5886554450881916) 22%, rgba(179,69,252,0.44579830223104866) 55%);
    .overlay__contenido{
        color: v.$blanco;
        h1{
            font-size: 5rem;
            font-weight: 700;
        }
        p{
            font-size: 2.5rem;
            background-color: v.$rojo;
            display: inline-block;
            padding: 2rem 1rem;
            font-weight: 700;
        }
        
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
}