html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", Arial, Helvetica;
}

h1, h2, h3, h4 {
  font-size: 2.8rem;
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

a {
  text-decoration: none;
}

.centrar-texto {
  text-align: center;
}

img {
  width: 100%;
}

p {
  font-size: 2.8rem;
  line-height: 1.5;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  font-size: 1.6rem;
}

/*Fijar navegacion efectoScroll*/
@media (min-width: 768px) {
  .fijar-barra {
    position: fixed;
    width: 100%;
    top: 0;
  }
}

@media (min-width: 768px) {
  .mover-body {
    margin-top: 8.6rem;
  }
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  background-color: #4cb8b3;
}
.header .cabecera {
  padding: 2rem 0;
  color: #ffffff;
}

@media (min-width: 768px) {
  .cabecera {
    display: flex;
    justify-content: space-between;
  }
}

.header__nav .enlace {
  display: block;
  font-size: 2.5rem;
  color: #ffffff;
  margin: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__nav .enlace {
    margin: auto;
  }
}
.header__nav .enlace:hover {
  color: #fef46d;
}
@media (min-width: 768px) {
  .header__nav {
    display: flex;
    gap: 2rem;
  }
}

.video {
  max-height: 70rem;
  min-height: 50rem;
  position: relative;
  overflow: hidden;
}
.video video {
  width: auto;
  height: 100%;
}
@media (min-width: 968px) {
  .video video {
    width: 100%;
    display: block;
  }
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(180, 58, 167);
  background: linear-gradient(140deg, rgba(180, 58, 167, 0.5886554451) 22%, rgba(179, 69, 252, 0.4457983022) 55%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.overlay .overlay__contenido {
  color: #ffffff;
}
.overlay .overlay__contenido h1 {
  font-size: 5rem;
  font-weight: 700;
}
.overlay .overlay__contenido p {
  font-size: 2.5rem;
  background-color: #f53756;
  display: inline-block;
  padding: 2rem 1rem;
  font-weight: 700;
}

.item {
  padding: 2rem 0;
}
.item p {
  font-size: 2rem;
}
.item .fecha {
  color: #f53756;
  font-weight: bold;
}
@media (min-width: 768px) {
  .item:last-child {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.contenedorInfo {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedorInfo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.lineup {
  padding: 3rem 0;
  background-color: #f53756;
}
.lineup h3 {
  color: #ffffff;
}
.lineup p {
  color: #ffffff;
  font-size: 2rem;
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .lineup .lineupGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.lineupGrid__item {
  padding: 1rem 0;
  margin: 2rem 0;
}
.lineupGrid__item p {
  color: #000000;
  margin-top: 3rem;
}
.lineupGrid__item li {
  font-weight: bold;
  padding: 2rem 1rem;
}
.lineupGrid__item li span {
  font-weight: 400;
}

.azul {
  background-color: #4cb8b3;
}

.amarillo {
  background-color: #fef46d;
}

.horario-amarillo li:nth-child(odd) {
  background-color: #fef03a;
}

.horario-azul li:nth-child(odd) {
  background-color: #3b9691;
}

.rock::after, .edm::after {
  content: "";
  width: 7rem;
  height: 7rem;
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto;
}

.rock::after {
  background-image: url("../../../build/img/icono_rock.png");
}

.edm::after {
  background-image: url("../../../build/img/icono_edm.png");
}

.contenedor {
  padding: 3rem 0;
}
.contenedor h3 {
  margin-bottom: 3rem;
}

.galeria-contenedor {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 468px) {
  .galeria-contenedor {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-contenedor {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 968px) {
  .galeria-contenedor {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay2 {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;
}
.overlay2 picture img {
  max-width: 70rem;
}
.overlay2 picture p {
  font-size: 2.5rem;
  background-color: #ffffff;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  text-align: center;
  margin: 4rem auto;
  font-weight: bold;
  cursor: pointer;
}

.fijar-body {
  overflow: hidden;
}

.boletos {
  background-color: #4cb8b3;
  padding: 3rem 0;
}
.boletos h3, .boletos p {
  color: #ffffff;
  text-align: center;
}

.boletosGrid .boletosGrid__item {
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .boletosGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contenidoBoletos p:first-child {
  background-color: #f53756;
  margin-top: 2rem;
}
.contenidoBoletos p:last-child {
  background-color: #752f97;
  font-weight: 700;
}

.contenidoBoletos-lista li {
  background-color: #ffffff;
  padding: 1rem 1rem;
}

.footer {
  background-color: #f53756;
  padding: 1.5rem 0;
  color: #ffffff;
}
.footer p {
  font-size: 2rem;
}