@use "base/variables" as v;
@use "base/mixins" as m;
.boletos{
    background-color: v.$azul;
    h3, p{
        color: v.$blanco;
        text-align: center;
    }
    padding: 3rem 0;
}
.boletosGrid{
    .boletosGrid__item{
        margin: 2rem 0;
    }
    @include m.tablet{
        @include m.gridd(2,2rem);
    }
}
.contenidoBoletos{
    p:first-child{
        background-color: v.$rojo;
        margin-top: 2rem;
    }
    p:last-child{
        background-color: v.$morado;
        font-weight: 700;
    }
}
.contenidoBoletos-lista{
    li{
        background-color: v.$blanco;
        padding: 1rem 1rem;
    }
}